import { Fog, Geometry, Mesh, PerspectiveCamera, PlaneGeometry, Scene, ShaderMaterial, TextureLoader, WebGLRenderer, } from 'three';
import Detector from '../lib/three.detector';
(function () {
    if (!Detector.webgl) {
        Detector.addGetWebGLMessage();
        return;
    }
    var mouseX = 0;
    var mouseY = 0;
    var startTime = Date.now();
    var windowHalfX = window.innerWidth / 2;
    var windowHalfY = window.innerHeight / 2;
    var vertexShader = "\nvarying vec2 vUv;\n\nvoid main() {\n\tvUv = uv;\n\tgl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );\n}\n";
    var fragmentShader = "\nuniform sampler2D map;\n\nuniform vec3 fogColor;\nuniform float fogNear;\nuniform float fogFar;\n\nvarying vec2 vUv;\n\nvoid main() {\n\tfloat depth = gl_FragCoord.z / gl_FragCoord.w;\n\tfloat fogFactor = smoothstep( fogNear, fogFar, depth );\n\n\tgl_FragColor = texture2D( map, vUv );\n\tgl_FragColor.w *= pow( abs(gl_FragCoord.z), 20.0 );\n\tgl_FragColor = mix( gl_FragColor, vec4( fogColor, gl_FragColor.w ), fogFactor );\n}\n";
    var container = document.getElementById('cd-main-overlay');
    container.style.background = 'linear-gradient(#1e4877, #4584b4, #4584b4)';
    var canvas = document.createElement('canvas');
    canvas.width = 32;
    canvas.height = window.innerHeight;
    var camera = new PerspectiveCamera(30, window.innerWidth / window.innerHeight, 1, 3000);
    camera.position.z = 3000;
    var scene = new Scene();
    var geometry = new Geometry();
    var texture = new TextureLoader().load('/images/cloud10.png', animateClouds);
    var fog = new Fog(0x4584b4, -100, 3000);
    var material = new ShaderMaterial({
        depthTest: false,
        depthWrite: false,
        transparent: true,
        uniforms: {
            fogColor: { type: 'c', value: fog.color },
            fogFar: { type: 'f', value: fog.far },
            fogNear: { type: 'f', value: fog.near },
            map: { type: 't', value: texture },
        },
        fragmentShader: fragmentShader,
        vertexShader: vertexShader,
    });
    var plane = new Mesh(new PlaneGeometry(64, 64));
    for (var i = 0; i < 8000; i++) {
        plane.position.x = Math.random() * 1000 - 500;
        plane.position.y = -Math.random() * Math.random() * 200 - 15;
        plane.position.z = i;
        plane.rotation.z = Math.random() * Math.PI;
        plane.scale.x = plane.scale.y = Math.random() * Math.random() * 1.5 + 0.5;
        plane.updateMatrix();
        geometry.merge(plane.geometry, plane.matrix);
    }
    var mesh1 = new Mesh(geometry, material);
    scene.add(mesh1);
    var mesh2 = new Mesh(geometry, material);
    mesh2.position.z = -8000;
    scene.add(mesh2);
    var renderer = new WebGLRenderer({
        alpha: true,
        antialias: false,
    });
    renderer.setSize(window.innerWidth, window.innerHeight);
    container.append(renderer.domElement);
    document.addEventListener('mousemove', function (event) {
        mouseX = (event.clientX - windowHalfX) * 0.25;
        mouseY = (event.clientY - windowHalfY) * 0.15;
    });
    window.addEventListener('resize', function () {
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
        renderer.setSize(window.innerWidth, window.innerHeight);
    });
    function animateClouds() {
        requestAnimationFrame(animateClouds);
        if (container.style.visibility !== 'hidden') {
            var position = ((Date.now() - startTime) * 0.03) % 8000;
            camera.position.x += (mouseX - camera.position.x) * 0.005;
            camera.position.y += (-mouseY - 70 - camera.position.y) * 0.01;
            camera.position.z = -position + 8000;
            renderer.render(scene, camera);
        }
    }
})();
